body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.nav-box {
  display: flex;
  justify-content: space-between;
  width: 89%;
  margin: 0px auto;
  padding: 15px 0px;
}
.logo {
  font-size: 20px;
  color: dodgerblue;
}
.bar {
  width: 28px;
  height: 2.5px;
  background-color: white;
  margin: 5px 0;
}
.icon {
  margin-right: 3px;
  cursor: pointer;
}
.menu-box {
  display: flex;
  justify-content: flex-end;
  background-color: rgb(29, 129, 230);
  padding: 2px 10px;
}
.menu {
  font-size: 20px;
  margin-right: 15px;
  color: white;
}
.slider-box {
  background-image: url("banner_background.jpg");
  background-size: cover;
  background-position: center;
  display: flex;
  padding: 50px 6px;
}
.head {
  color: darkcyan;
}
.old {
  color: gray;
  text-decoration: line-through;
}
span {
  margin: 12px 8px;
  color: red;
}
.apple6 {
  width: 110%;
  position: relative;
}
.image {
  margin-right: 24px;
}
p {
  z-index: 999;
  color: gray;
}
.now {
  background-color: rgb(29, 129, 230);
  text-align: center;
  border-style: none;
  padding: 10px 18px;
  color: white;
  border-radius: 6px;
  margin-left: 10px;
}

.info {
  margin-left: 4px;
}

.free {
  position: absolute;
  left: 10%;
  top: 32%;
}
.delivery {
  position: relative;
  width: 90%;
  box-shadow: 2px 2px 4px lightslategray;
  margin: 18px auto;
  border-radius: 4px;
  padding: 6px;
}
.deliv-para {
  font-size: 14px;
  padding-bottom: 10px;
}
.deliv {
  width: 60%;
  margin-left: 100px;
  line-height: 0.7px;
}
h5 {
  font-size: 14.5px;
}
.delivery-box {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  margin-top: 40px;
}

.apear {
  position: fixed;
  top: 70px;
  right: 0;
  width: 0;
  background-color: rgb(29, 129, 230);
  height: 100%;
  overflow: scroll;
  padding-top: 8px;
  transition: all 0.3s;
}

a {
  color: white;
  text-decoration: none;
  font-size: 19px;
  margin-left: 25px;
}
li {
  list-style-type: none;
  margin-top: 10px;
}
input {
  width: 80%;
  border: none;
  background-color: rgb(29, 129, 230);
  border-bottom: 2px solid white;
  color: white;
  padding: 8px;
  margin-left: 18px;
  margin-bottom: 24px;
  font-size: 18px;
}

::placeholder {
  color: white;
  font-size: 18px;
}
h4 {
  text-align: center;
  color: white;
  font-size: 20px;
  font-weight: 600;
}

span.close {
  font-size: 22px;
  color: white;
  position: relative;
  padding: 8px 17px;
  cursor: pointer;
}
.loop {
  position: absolute;
  right: 32px;
  top: 128px;
}

.deal {
  color: black;
  text-align: left;
}

.cask-flex,
.cask-flex2 {
  display: flex;
  justify-content: space-between;
}

.cask-flex2 {
  margin-top: -38px;
}
.cask-head {
  color: black;
}
.cask-head2 {
  color: red;
}

.cask-box {
  width: 90%;
  box-shadow: 2px 2px 14px gainsboro;
  margin: 2px 8px;
  padding: 2px 14px;
  box-sizing: border-box;
}
.pro-bar {
  width: 95%;
  height: 8px;
  border-radius: 25px;
  background-color: gainsboro;
  margin: 0px auto;
  margin-bottom: 16px;
}
.prog {
  width: 70px;
  height: 8px;
  border-radius: 25px;
  background-color: rgb(29, 129, 230);
}
.line {
  display: flex;
  border: 1px solid gray;
  border-radius: 6px;
  margin-right: 2px;
}
.hurry {
  color: black;
}

.extra {
  margin-top: -8px;
}
.lie {
  margin: 3px 0px;
  border-right: 1px solid gray;
  padding: 6px 16px;
  text-align: center;
}
.lie-2 {
  padding: 6px 16px;
  text-align: center;
  margin: 3px 0px;
}

.hour {
  display: flex;
}

.hr,
.min,
.sec {
  font-size: 10px;
  margin: 2px 8px;
}
.right {
  margin-top: 16px;
}

nav {
  display: flex;
  border-bottom: 2px solid gainsboro;
  padding: 10px;
  padding-bottom: 18px;
  box-sizing: border-box;
  width: 90%;
  margin: 130px auto;
  margin-bottom: 0px;
}
.huawei {
  margin-top: 56px;
}

.image-cart {
  text-align: center;
  padding-bottom: 0px;
  width: 90%;
  margin: 10px auto;
  border-radius: 0px 0px 10px 10px;
  box-shadow: 2px 2px 10px gray;
  overflow: hidden;
}
.image-para {
  color: red;
}
.image-text {
  color: black;
}

.nav-btn {
  margin: 0px 9px;
  position: relative;
  padding-bottom: 6px;
}
.nav-btn::before {
  content: "";
  position: absolute;
  height: 3px;
  width: 0%;
  bottom: 0;
  left: 0;
  background-color: deeppink;
  transition: all 0.3s;
}

.nav-btn:hover::before {
  width: 100%;
}
.hua {
  margin-top: 12px;
  width: 100%;
}

.image-flex {
  display: flex;
  justify-content: center;
}
.ball {
  width: 8px;
  height: 8px;
  padding: 6px;
  border-radius: 50%;
  margin: 8px 4px;
  background-color: black;
}
.one {
  background-color: gainsboro;
}
.two {
  background-color: aqua;
}
.add-btn {
  background-color: blue;
  color: white;
  padding: 15px 0px;
  font-size: 20px;
  display: block;
  width: 100%;
  border-style: none;
  transition: all 0.3s;
  margin-top: 16px;
}
.add-btn:active {
  background-color: green;
}

.ultra {
  display: none;
  transition: all 0.5s;
  animation: show 0.5s ease-in;
}
@keyframes show {
  from {
    transform: scale(0.8);
    opacity: -1;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}
#first {
  display: block;
}

.cask-slide {
  margin-top: 40px;
  display: flex;
  width: 100%;
  overflow: hidden;
  overflow-x: scroll;
}

.cask-slide::-webkit-scrollbar {
  width: 0;
}

* {
  scroll-behavior: smooth;
}

.pop {
  color: black;
  text-align: left;
  font-size: 22px;
  margin-left: 16px;
  font-weight: 400;
}
.pop-para {
  color: blue;
  text-align: left;
  margin-left: 16px;
}

.pop-img {
  text-align: center;
  padding: 40px;
}

.pop-box {
  box-shadow: 2px 2px 8px gray;
  overflow: hidden;
  overflow-x: scroll;
  text-align: center;
  width: 97%;
  margin: 2px auto;
  display: flex;
}
.blu {
  background-color: dodgerblue;
}
.pop-bg {
  text-align: center;
  min-width: 68%;
}

.pop-box::-webkit-scrollbar {
  width: 0;
}

.tel-para {
  color: black;
  margin-top: -20px;
}

section {
  margin-top: 100px;
}

.see {
  box-shadow: 2px 2px 8px gray;
  width: 98%;
  height: 50vh;
  overflow: hidden;
  overflow-y: scroll;
  transition: all 0.8s ease-in;
  margin-bottom: 12px;
  display: none;
  animation: swim 0.5s ease-in-out;
  margin-left: auto;
  margin-right: auto;
}

@keyframes swim {
  from {
    height: 0;
  }
  to {
    height: 50vh;
  }
}

.cover {
  width: 45px;
  border: 1px solid gray;
  height: 25px;
  border-radius: 50%;
  overflow: hidde;
  background-color: gainsboro;
}
.pomme {
  width: 19px;
  height: 19px;
  background-color: white;
  border-radius: 50%;
  /* transform: translateX(24px); */
  border: 2px dashed blue;
  transition: all 0.4s ease-in;
}

.theme {
  margin-right: auto;
}

#number {
  width: 18px;
  height: 18px;
  border: 1px solid black;
  border-radius: 25px;
  text-align: center;
  padding: 4px;
  color:white;
  background-color: dodgerblue;
  margin-top: -30px;
  margin-left: 14px;
  font-weight: 600;
  animation: number 0.6s ease-out infinite alternate;

}

@keyframes number {
  from{
transform: translateX(0);
  }
  to{
transform: translateX(13px);


  }
  
}

.see-para {
  color: black;
}
.perso {
  font-size: 14px;
}

#error {
  display: flex;
position: relative;
  justify-content: space-between;
}
.error-img {
  display: block;
  width: 100%;
  margin-top: 12px;
}
.err {
  box-shadow: 2px 4px 8px black;
  padding-bottom: 5px;
  display: none;
  transition: all 0.5s;
}

.error-btn {
  display: block;
  width: 97%;
  padding: 12px;
  background-color: orange;
  transition: all 0.3s;
  color: white;
  margin: 0px auto;
  margin-top: 44px;
  border-radius: 10px;
  border-style: none;
  font-size: 20px;
  padding: 12px;
  box-sizing: border-box;
  font-family: "Times New Roman", Times, serif;
}

.error-para {
  font-size: 20px;
  color: dodgerblue;
}

.error-right {
  margin-right: 12px;
}

.error-price {
  margin-top: -15px;
