  padding-right: 12px;
  margin-top: -10px;
}

.hero {
  width: 100px;
  width: 105px;
  border-radius: 8px;
  overflow: hidden;
  margin-left: 12px;
}

.now:active {
  background-color: greenyellow;
  color: white;
  transition: all 0.3s;
}
.error-btn:active {
  background-color: dodgerblue;
}


